import { combineReducers } from 'redux'
import {ascertainingReducer} from './ascertaining_mark'
import {formativeReducer} from "./formative_mark";
import {creativeReducer} from "./creative_mark";
import {wish_mark_Reducer} from "./wish_mark";


export const rootReducer  =
    combineReducers({
        formative_marks:  formativeReducer,
        creative_marks: creativeReducer,
        ascertaining_marks: ascertainingReducer,
        wish_mark: wish_mark_Reducer,
    });
