export const ADD_CREATIVE_MARK = 'ADD_CREATIVE_MARK';
export const SET_CREATIVE_MARK = 'SET_CREATIVE_MARK';
export const ADD_FORMATIVE_MARK = 'ADD_FORMATIVE_MARK';
export const SET_FORMATIVE_MARK = 'SET_FORMATIVE_MARK';
export const ADD_ASCERTAINING_MARK = 'ADD_ASCERTAINING_MARK';
export const SET_ASCERTAINING_MARK = 'SET_ASCERTAINING_MARK';
export const SET_WISH_MARK = 'SET_WISH_MARK';


export function addMark(mark, type) {
    switch (type) {
        case 'CREATIVE':
            return {
                type: ADD_CREATIVE_MARK,
                payload: mark,
            };
        case 'FORMATIVE':
            return {
                type: ADD_FORMATIVE_MARK,
                payload: mark,
            };
        case 'ASCERTAINING':
            return {
                type: ADD_ASCERTAINING_MARK,
                payload: mark,
            };
        default:
            return {
                type: ADD_FORMATIVE_MARK,
                payload: mark,
            }
    }

}
export function setMark(mark, index, type='FORMATIVE') {
    switch (type) {
        case 'CREATIVE':
            return {
                type: SET_CREATIVE_MARK,
                payload: {
                    mark: mark,
                    index: index,
                }
            };
        case 'FORMATIVE':
            return {
                type: SET_FORMATIVE_MARK,
                payload: {
                    mark: mark,
                    index: index,
                }
            };
        case 'ASCERTAINING':
            return {
                type: SET_ASCERTAINING_MARK,
                payload: {
                    mark: mark,
                    index: index,
                }
            };
        case 'WISH':
            return {
                type: SET_WISH_MARK,
                payload: mark,
            };
        default:
            return {
                type: SET_FORMATIVE_MARK,
                payload: {
                    mark: mark,
                    index: index,
                }
            }
    }

}