import {ADD_ASCERTAINING_MARK, SET_ASCERTAINING_MARK} from '../actions/MarksActions'

export const initialState = [2];

export function ascertainingReducer(state=initialState, action) {
    let new_state;
    switch (action.type) {

        case ADD_ASCERTAINING_MARK:
            new_state = Object.assign([], state);
            new_state.push(action.payload);
            return new_state;

        case SET_ASCERTAINING_MARK:
            new_state = Object.assign([], state);
            new_state[action.payload.index] = Number(action.payload.mark);
            return new_state;

        default:
            return state;
    }
}