import React, { Component } from 'react';
import '../App.css';
import Mark from '../components/Mark'
import {connect} from "react-redux";
import {addMark, setMark} from '../actions/MarksActions'
import Mean from '../components/Mean';
import Wish from '../components/Wish';
import Need from '../components/Need';

class App extends Component {


  addField = (type) => {
    this.props.addMark(null, type);
  };

    render() {
    return (
        <div>
          <h1>Система расчета необходимого количества оценок</h1>
          <div className="marks">
              <div id="ascertaining" className="mark_type">
              <h2 className="type_name">Констатирующие:</h2>
                  {this.props.ascertaining_marks.map((mark, index) =>
                      <Mark index={index} key={index} mark={mark} type='ASCERTAINING' setMark={this.props.setMark}/>)}
                <div className="mark" onClick={() =>this.addField('ASCERTAINING')}>+</div>
              </div>
              <div id="formative" className="mark_type">
                  <h2 className="type_name">Формирующие:</h2>
                  {this.props.formative_marks.map((mark, index) =>
                      <Mark  index={index} key={index} type='FORMATIVE' mark={mark} setMark={this.props.setMark}/>)}
                  <div className="mark"  onClick={() =>this.addField('FORMATIVE')}>+</div>
              </div>
              <div id="creative" className="mark_type">
                  <h2 className="type_name">Творческие:</h2>
                  {this.props.creative_marks.map((mark, index) =>
                      <Mark index={index} key={index} mark={mark} type='CREATIVE' setMark={this.props.setMark}/>)}
                  <div className="mark" onClick={() =>this.addField('CREATIVE')}>+</div>
              </div>
          </div>
            <Mean staff={this.props}/>
            <Wish setMark={this.props.setMark} mark={this.props.wish_mark}/>
            <Need staff={this.props}/>
        </div>
    );
  }
}

const mapStateToProps = store => {
    console.log(store);
    return {
        ascertaining_marks: store.ascertaining_marks,
        formative_marks: store.formative_marks,
        creative_marks: store.creative_marks,
        wish_mark: store.wish_mark,
    }
};
const mapDispatchToProps = dispatch => {
    return {
        addMark: (mark, type) => dispatch(addMark(mark, type)),
        setMark: (mark, index, type) => dispatch(setMark(mark, index, type)),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
