import React, { Component } from 'react';

class Need extends Component {
    state = {
        isReachable: false,
        warning: ''
    };

    count = () => {
        const average = (array) => array.reduce((a, b) => a + b) / array.length;
        const {ascertaining_marks, formative_marks, creative_marks, wish_mark} = this.props.staff;
        let marks = {};
        let isReachable = false;
        this.setState({isReachable: false});
        for(let i=wish_mark; i<=5; i++) {
            let added = 0;
            let ascertaining_marks_clone = Object.assign([], ascertaining_marks);
            let formative_marks_clone = Object.assign([], formative_marks);
            let creative_marks_clone = Object.assign([], creative_marks);
            let mean_mark = 0.2 * average(creative_marks) + 0.6 * average(ascertaining_marks) + 0.2 * average(formative_marks);
            //console.log(added, mean_mark);
            let mark_type = document.getElementById("mark_type_select").value;
            while (added < 20) {
                switch (mark_type) {
                    case "ascertaining":
                        ascertaining_marks_clone.push(Number(i));break;
                    case "formative":
                        formative_marks_clone.push(Number(i));break;
                    case "creative":
                        creative_marks_clone.push(Number(i));break;
                    default:
                        ascertaining_marks_clone.push(Number(i));break;
                }

                mean_mark = 0.2 * average(creative_marks_clone)
                    + 0.6 * average(ascertaining_marks_clone) + 0.2 * average(formative_marks_clone);
                mean_mark.toFixed(2);
                added += 1;
                //console.log(added, mean_mark);
                if ((wish_mark-0.5) <= mean_mark) break;
            }
            if (0 < added && added < 20) {
                console.log(added, i);
                isReachable = true;
                marks[i] = added;
            }
        }

        this.setState({isReachable: isReachable, marks: Object.entries(marks), warning: 'Оценка недостижима'});

    };
    render() {
        return (
            <div>
                <select name="mark" id="mark_type_select">
                    <option value="ascertaining">Констатирующая</option>
                    <option value="formative">Формирующая</option>
                    <option value="creative">Творческая</option>
                </select>
                <button onClick={this.count}>Посчитать необходимое количество</button>
                {this.state.isReachable?
                    <div className="possible-marks">
                        <div className="mark_type">
                            <div className="mark-wrapper">
                                <h3>Оценка</h3>
                                <h3>Необходимое количество</h3>
                            </div>

                            {this.state.marks.map((arr) =>
                                    <div key={arr[0]} className="mark-wrapper">
                                    <div className="mark">
                                        {arr[0]}
                                    </div>
                                    <div className="mark">
                                        {arr[1]}
                                    </div>
                                </div>
                                )
                            }
                        </div>

                    </div>:this.state.warning}
            </div>
        )
    }
}


export default Need;