import React, { Component } from 'react';

class Mean extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };

    }

    render() {
        const average = (array) => array.reduce((a, b) => a + b) / array.length;
        const {ascertaining_marks, formative_marks, creative_marks,} = this.props.staff;
        const mean_mark = 0.2 * average(creative_marks) + 0.6 * average(ascertaining_marks) + 0.2 * average(formative_marks);
        return(
            <div id="mean_wrapper">
                <h2>Среднее значение</h2>
                <h3>{mean_mark.toFixed(2)}</h3>
            </div>)

    }
}

export default Mean;