import {ADD_CREATIVE_MARK, SET_CREATIVE_MARK} from '../actions/MarksActions'

export const initialState = [2];

export function creativeReducer(state=initialState, action) {
    let new_state;
    switch (action.type) {

        case ADD_CREATIVE_MARK:
            new_state = Object.assign([], state);
            new_state.push(action.payload);
            return new_state;

        case SET_CREATIVE_MARK:
            new_state = Object.assign([], state);
            new_state[action.payload.index] = Number(action.payload.mark);
            return new_state;

        default:
            return state;
    }
}