import React, { Component } from 'react';

class Mark extends Component{
    state = {
        isActive: false,
    };

    setActive = () => {
        this.setState({isActive : true})
    };
    setInactive = (event) => {
        if(event.key === 'Enter' || event.key === 'Escape') {
            this.setState({isActive: false})
        }
    };
    chooseRight = (event) => {
        let text = event.target.value;
        if(text) {
            if (text.match('[0-5]')) {
                this.props.setMark(text, this.props.index, this.props.type);
                this.setState({isActive: false})
            }
            else {
                event.target.value = "";
            }
        }

    };

    render() {
        return <div onClick={this.setActive} onKeyDown={this.setInactive} className="mark" type="text">
            {this.state.isActive || this.props.mark === null?
                <input type="text" onChange={this.chooseRight} maxLength={1} autoFocus={true}/>:
                this.props.mark}</div>
    }
}

export default Mark;